.fondo-login{
    display:flex;
    flex-flow: column nowrap;
    background:rgb(226, 226, 226);
    width: 100%;
}
.content-login{
    display:flex;
    flex-flow: column nowrap;
    width: 35%;
    margin-left:32.5%;
    margin-right:32.5%;
    justify-content:center;
    align-items: center;
    margin-top:20px;
    margin-bottom:50px;
    padding-top:20px;
    border-radius: 15px;
    background:rgb(255, 255, 255);
    box-shadow: 6px 7px 7px rgba(0, 0, 0, 0.199);
}

    .content-login h1{
        margin-bottom: 40px;
    }

    .content-login img{
        width: 150px;
        margin-bottom: 20px;
        border-radius: 50%;
    }


.content-login form{
    display:flex;
    flex-flow: column nowrap;
    width: 100%;
    justify-content:center;
    align-items: center;
}
    .content-login #label{
        margin-bottom: 5px;
        font-size:25px;
    }

    ::placeholder{
        text-align: center;
        color:rgba(0, 0, 0, 0.438);
    }

    :focus::-webkit-input-placeholder {
        opacity: 0;
        transform: translate(70%);
        -webkit-transition: all 0.65s ease-in-out;
        transition: all 0.90s ease-in-out;
      }


    .content-login #email{
        width: 350px;
        margin-bottom: 20px;
        border:none;
        border-radius: 5px;
        background:rgb(235, 235, 235);
        padding:11px;
        font-size:19px;
    }
    

    .content-login #password{
        width: 350px;
        margin-bottom: 40px;
        border:none;
        border-radius: 5px;
        background:rgb(235, 235, 235);
        padding:11px;
        font-size:19px;
    }

    .content-login #send{
        width: 350px;
        margin-bottom: 15px;
        border:none;
        border-radius: 5px;
        background:rgb(62, 96, 250);
        padding:8px;
        font-size:26px;
        color:white;
    }

    .content-login #send:hover{
        background:rgb(99, 125, 245);
        color:rgb(219, 219, 219);
        cursor: pointer;
    }

    .content-login #reset-password{
        width: 350px;
        margin-bottom: 50px;
        text-align: right;
        padding:5px;
        font-size:19px;
        color:blue;
    }

    .content-login #reset-password:hover{
        color:red;
    }



    





