.content-header{
    display:flex;
    flex-flow: row nowrap;
    justify-content:center;
    align-items:center;
    width: 100%;
    background:rgb(139, 4, 4);
    height: 70px;
    
}


.content-header a{
    color:white;
    font-size:26px;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing:1px;
}

.content-header a:hover{
    color:rgb(196, 253, 89);
}