.content-footer{
    display:flex;
    flex-flow: row nowrap;
    justify-content:center;
    align-items:center;
    width: 100%;
    background:rgb(139, 4, 4);
    height: 80px;
    
}


.content-footer a{
    color:white;
    font-size:17px;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing:1px;
}

.content-footer a:hover{
    color:rgb(196, 253, 89);
}