.content-navbar-left{
    width: 17%;
    background:white;
    display: flex;
    flex-flow: column nowrap;
}


    .content-navbar-left i{
        margin-right: 10px;
        width: 40px;
        height: 40px;
        font-size: 21px;
        padding:12px;
        text-align: center;
        align-items: center;
        justify-content: center;
        display: flex;
    }

    .content-navbar-left a{
       color:black;
       display:flex;
       flex-flow: row nowrap;
       width: 100%;
       justify-content:flex-start;
       align-items: center;
       
    }

    .content-navbar-left a:hover{
        background:rgb(252, 240, 240);
    }

    .content-navbar-left h3{
        font-weight: normal;
        width: 90%;
        font-size: 15px;
    }

.class_active{
    background:rgb(252, 240, 240);
    
}

@media (max-width:770px){
    .content-navbar-left{
        width: 100%;
        overflow: hidden;
       margin-left: -30px;
    }

    .content-navbar-left i{
        margin-right: 5px;
        padding-left:45px;
    }


    .content-navbar-left h3{
        padding-left:50px;
        font-size: 20px;
    }
}