.fondo-forgot-password{
    display:flex;
    flex-flow: column nowrap;
    background:rgb(226, 226, 226);
    width: 100%;
}

.content-forgot-password{
    display:flex;
    flex-flow: column nowrap;
    width: 35%;
    margin-left:32.5%;
    margin-right:32.5%;
    justify-content:center;
    align-items: center;
    margin-top:20px;
    margin-bottom:50px;
    padding-top:0px;
    border-radius: 15px;
    background:rgb(255, 255, 255);   
    box-shadow: 6px 7px 7px rgba(0, 0, 0, 0.199);
   
}


.content-forgot-password h1{
    margin-bottom: 30px;
    font-size:28px;
}

.content-forgot-password img{
    width: 200px;
    margin-bottom: 5px;
    border-radius: 50%;
}

.content-forgot-password p{
    width: 95%;
    font-size:20px;
    text-align: center;
    margin-bottom: 20px;
}

.content-forgot-password form{
    display:flex;
    flex-flow: column nowrap;
    width: 100%;
    justify-content:center;
    align-items: center;
}
    .content-forgot-password #label{
        margin-bottom: 5px;
        font-size:25px;
    }

    ::placeholder{
        text-align: center;
        color:rgba(0, 0, 0, 0.438);
    }

    :focus::-webkit-input-placeholder {
        opacity: 0;
        transform: translate(70%);
        -webkit-transition: all 0.65s ease-in-out;
        transition: all 0.90s ease-in-out;
      }


    .content-forgot-password #email{
        width: 350px;
        margin-bottom: 20px;
        border:none;
        border-radius: 5px;
        background:rgb(235, 235, 235);
        padding:11px;
        font-size:19px;
    }

    .content-forgot-password #password{
        width: 350px;
        margin-bottom: 40px;
        border:none;
        border-radius: 5px;
        background:rgb(235, 235, 235);
        padding:11px;
        font-size:19px;
    }

    .content-forgot-password #send{
        width: 350px;
        margin-bottom: 15px;
        border:none;
        border-radius: 5px;
        background:rgb(62, 96, 250);
        padding:8px;
        font-size:26px;
        color:white;
    }

    .content-forgot-password #send:hover{
        background:rgb(99, 125, 245);
        color:rgb(219, 219, 219);
        cursor: pointer;
    }



.footer{
    display:flex;
    flex-flow: row nowrap;
    background:rgb(15, 43, 165);
    width: 100%;
    
}

    .footer i{
        text-align: center;
        color:white;
        font-size: 25px;
        padding: 10px;
    }
    
    .footer a{
    text-align: center;
    color:white;
    font-size: 25px;
    width: 100%;
    padding: 10px;
    font-family: arial;
    }

    .footer:hover{
        background:rgb(25, 55, 190);
    }

    

    



