.fondo-dashboard{
    display:flex;
    flex-flow: row nowrap;
    background:rgb(226, 226, 226);
    width: 100%;
    
    
}
.content-dashboard-main{
    width: 81.5%;
    display:flex;
    flex-flow: column nowrap;
    margin-top:1%;
    margin-left:1%;
    padding-left: 12px;
    padding-top:20px;
    border-top: 3px solid rgb(175, 12, 12);
    border-radius: 5px;
    background:white;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.062);
    margin-bottom: 20px;
    padding-bottom: 40px;
}

.content-dashboard-main h2{
    text-transform: capitalize;
    font-size: 29px;
    font-weight: normal;
    padding-left: 10px;
}

.content-dashboard-main label{
    text-transform: capitalize;
    font-size: 18px;
    font-weight: bold;
    margin-bottom:5px;
}

.content-dashboard-options{
    margin-top:20px;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
}

.content-dashboard-target{
    width: 46%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: flex-start;
    margin-left: 1%;
    margin-right: 2%;
    margin-bottom: 2%;
  
}

#azulA{
    background-color:#00c0ef;
}

#naranjaA{
    background-color: #f39c12;
}

#rojoA{
    background-color: #dd4b39;
}

#verdeA{
    background-color: #00a65a;
}

.content-dashboard-target:hover{
    background-color:rgba(8, 109, 241, 0.884);
}

.content-dashboard-target-info{
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content:center;
    align-items: center;
}

.content-dashboard-target-img{
    overflow: hidden;
    width: 80%;
    height: 110px;
    display: flex;
    flex-flow: row nowrap;
    justify-content:flex-end;
    align-items: right;
    text-align: right;
}

.content-info{
    width: 100%;
    height: 110px;
    display: flex;
    flex-flow:column nowrap;
    text-align: left;
    padding-left: 20px;
    padding-top: 10px;
}

.content-info a{
    color:white;
}

.content-info a>h1:hover{
    color:rgb(221, 221, 221);
}

.content-info a>h3:hover{
    color:rgb(221, 221, 221);
}

.content-info h1{
    text-transform: capitalize;
    margin-bottom: 15px;
}

.content-info h3{
    text-transform: capitalize;
    font-weight: normal;
    letter-spacing:1.1px;
}

.content-dashboard-target-img img{
    width: 100px;
    height: 100px;
    padding:10px;
}

.content-dashboard-target-img img:hover{
    width: 115px;
    height: 115px;
    transition-duration: .5s;
}

.content-dashboard-vermas{
    width: 100%;
    height: 30px;
    display: flex;
    flex-flow: row nowrap;
    justify-content:center;
    align-items:center;
    padding:2px;
    text-align: center;
    font-size: 22px;
}

#azulB{
    background-color: #00acd7;
}

#naranjaB{
    background-color: #da8c10;
}

#rojoB{
    background-color: #c64333;
}

#verdeB{
    background-color: #009551;
}

.content-dashboard-vermas a{
    color:rgba(218, 218, 218, 0.904);
}

.content-dashboard-vermas h4{
    font-weight: normal;
}

.content-dashboard-vermas a:hover{
    cursor: pointer;
    color:white;
}



@media (max-width:1133px){
    .content-info h1{
        font-size: 28px;
    }
}

@media (max-width:1003px){
    .content-info h1{
        font-size: 25px;
    }
}


@media (max-width: 991px){
    .content-info h3{
        font-size: 18px;
    }
}


@media (max-width: 960px){
    .content-info h1{
        font-size: 23px;
    }
    .content-info h3{
        font-size: 15px;
    }
}

@media (max-width: 950px){
    
    .content-dashboard-target{
        width: 44%;
    }
    
    .content-dashboard-target-img img{
        width: 96x;
        height: 96px;
        padding:9px;
    }
    
    
    .content-dashboard-vermas h4{
        font-size: 18px;
    }
    
    }

@media (max-width: 877px){
    .content-info h1{
        font-size: 21px;
    }
}

@media (max-width: 877px){
    .content-info h3{
        font-size: 14px;
    }
}

@media (max-width: 824px){
    .content-info h3{
        font-size: 13px;
    }

    .content-dashboard-vermas h4{
        font-size: 15px;
    }

    .content-dashboard-main h2{
        font-size: 20px;
    }
}

@media (max-width:770px){
    .fondo-dashboard{
        width: 102.5%;
    }
    .content-dashboard-main{
    width: 100%;
    margin-left:5%;
    margin-top:3%;
    padding-left: 3px; 
    }

    .content-dashboard-main label{
        padding-left: 7px;  
    }

    .content-dashboard-main h2{
        padding-left: 18px;  
    }
    
}


@media (max-width: 729px){
    .content-info h3{
        font-size: 12px;
    }
}

@media (max-width: 715px){
    .content-dashboard-options{
        flex-flow: column wrap;
    }
    .content-dashboard-target{
        width: 95%;
       
    }
    
    .content-dashboard-target-img img{
        width: 107x;
        height: 107px;
        padding:5px;
    }
    
    .content-info h1{
        font-size: 25px;
    }

    .content-info h3{
        font-size: 20px;
    }

    .content-dashboard-vermas h4{
        font-size: 20px;
    }

    

    .content-dashboard-vermas h4{
        font-size: 20px;
    }


}

@media (max-width: 680px){

    
    .content-dashboard-main label{
        font-size: 17px;
    }

    .content-dashboard-main h2{
        font-size: 17px;
    }

    .content-dashboard-target{
        margin-bottom: 15px;
        border-radius: 5px;
    }

    .content-dashboard-vermas{
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
    }


}

@media (max-width: 500px){
    .content-dashboard-options{
        flex-flow: column wrap;
    }
    .content-dashboard-target{
        width: 90%;
    }
    .content-dashboard-target-img{
       display:none;
    }

    .content-info h1{
        justify-content:center;
        align-items:center;
        text-align: center;
    }

    .content-info h3{
        justify-content:center;
        margin-top:-6px;
        align-items:center;
        text-align: center;
    }

    .content-dashboard-vermas{
        margin-top:-25px;
        height: 40px;
    }
    .content-dashboard-vermas h4{
        font-size: 18px;
    }

}

@media (max-width: 400px){

    .content-dashboard-main label{
        font-size: 20px;
    }

    .content-dashboard-main h2{
        font-size: 25px;
    }
}