*{
    text-decoration: none;
    list-style: none;
}

.content-navbar{
    display:flex;
    flex-flow: column nowrap;
    width: 100%;
    background:rgb(255, 240, 240);
}

.content-navbar-top{
    background:rgb(93, 80, 204);
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items:center;
    width: 100%;
    height: 55px;
}

    .content-navbar-menu{
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items:center;
    }


    .content-navbar-menu i:hover{
        background:rgb(110, 100, 201);
        cursor: pointer;
    }

    .content-navbar-menu i{
        width: 55px;
        height: 55px;
        padding-top: 10px;
        font-size: 35px;
        text-align: center;
        color:white;
    }

    .content-navbar-menu h2{
        padding-top: 15px;
        width: 207px;
        height: 55px;
        color:white;   
        text-align: center; 
    }

    .content-navbar-menu h2:hover{
        background:rgb(110, 100, 201);
        cursor: pointer;    
    }

    
.content-navbar-profile{
    padding: 13px;
    width: 100;
    height: 55px;
  
}
    .content-navbar-profile:hover{
        background:rgb(110, 100, 201);
    }

    .content-navbar-profile i{
        margin-right:10px;
        font-size: 25px;
        color:white;
        cursor: pointer;
    }


    #nom-abrev{
        text-align: center;
        margin-right:20px;
        font-size: 22px;
        color:white;
        text-transform: capitalize;
    }

    #nom-abrev1{
        text-align: center;
        margin-right:20px;
        font-size: 22px;
        color:white;
        text-transform: capitalize;
    }

    #nom-abrev1:hover{
        cursor: pointer;
        text-align: center;
        margin-right:20px;
        font-size: 22px;
        color:white;
        text-transform: capitalize;
    }

    .content-navbar-profile li > ul{
        display: none;
    }

    .li{
       display:flex;
       flex-flow:column nowrap;
       justify-content:center;
       align-items:center;
       text-align: center;
       position: relative;
    }

    .li > label{
        font-size: 16px;
        margin-bottom: 7px;
        color:white;
    }

    #perfil{
        margin-bottom: 15px;
        text-transform: capitalize;
    }

    #btn{
        background:rgba(158, 5, 5, 0.815);
        color:white;
        border-radius: 5px;
        padding:5px;
        width: 260px;
        text-align:center;
        border:none;
        font-weight:normal;
        font-size: 22px;
    }

    #btn:hover{
        background:rgba(216, 13, 13, 0.815);
        cursor: pointer;
    }

    .btn{
        display:flex;
        flex-flow:column nowrap;
        justify-content:center;
        align-items:center;
        text-align: center;
        width: 286px;
        height: 60px;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        background:white;
    }


    .content-navbar-profile li > img{
        width: 130px;
        height: 130px;
        border-radius: 50%;
        margin-bottom: 15px;
    }


    .content-navbar-profile li:hover > ul{
        background:rgb(93, 80, 204);
    }

@media (max-width:770px){
    .content-navbar-menu{
        flex-flow: row-reverse;
    }

    .content-navbar-top{
        height: 75px;
    }

    .content-navbar-menu i{
        width: 75px;
        height: 75px;
        padding-top: 18px;
        padding-left: 1px;
        font-size: 40px;
    }

    .content-navbar-menu h2{
        padding-top: 23px;
        margin-left: auto;
        margin-right: auto;
        width: 150px;
        height: 75px;
        font-size: 25px;
    }

    .content-navbar-profile{
        padding-top: 21px;
        height: 75px;  
    }

    .content-navbar-profile i{
        margin-right:10px;
        font-size: 32px;
    }

    #nom-abrev1{
        font-size: 25px;
    }

    #nom-abrev{
        font-size: 19px;
    }

}
